<template lang="pug">
    b-badge(:variant="getStatusVariant(value)") {{getStatus(value)}}
</template>
<script>
export default {
    name: 'status-active',
    methods: {
        getStatus(_value) {
            return _value == true ? 'ACTIVE' : 'INACTIVE'
        },
        getStatusVariant(_value) {
            return _value == true ? 'success' : 'secondary'
        },
    },
    props: {
        value: {
            type: Boolean,
            default: '',
            validator: function (value) {
                return [true, false, ''].indexOf(value) !== -1
            }
        }
    }
}
</script>