import config from './employee-list.config.json'
import StatusActive from '@/components/StatusActive'
export default {
    name: 'employee-list',
    data() {
        return {
            config,
            employees: [],
            error: ''
        }
    },
    components: {
        StatusActive
    },
    methods: {
        getEmployees() {
            this.$http({
                method: 'get',
                url: '/employee/view/list-by-admin',
                headers: {
                    Authorization: 'Bearer ' + this.$store.getters.getToken
                }
            })
            .then(response => {
                this.employees = response.data;
            })
            .catch(error => {
                this.error = error.response
            })
        }
    },
    created() {
        this.getEmployees()
    }
}